"use client"

import { useState } from 'react'
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"

const questions = [
  {
    id: 1,
    level: 'easy',
    question: 'Which part of the cell is known as the powerhouse? ü¶†',
    options: ['Nucleus', 'Mitochondria', 'Ribosome', 'Cell Wall'],
    correctAnswer: 'Mitochondria'
  },
  {
    id: 2,
    level: 'easy',
    question: 'What do plants need for photosynthesis? üå±',
    options: ['Only water', 'Only sunlight', 'Water and sunlight', 'None of these'],
    correctAnswer: 'Water and sunlight'
  },
  {
    id: 3,
    level: 'medium',
    question: 'Which of these is a part of DNA? üß¨',
    options: ['Nucleus', 'Adenine', 'Cell Wall', 'Chlorophyll'],
    correctAnswer: 'Adenine'
  },
  {
    id: 4,
    level: 'medium',
    question: 'What type of cell has no nucleus? üß´',
    options: ['Plant cell', 'Animal cell', 'Bacterial cell', 'Fungal cell'],
    correctAnswer: 'Bacterial cell'
  },
  {
    id: 5,
    level: 'hard',
    question: 'Which process produces ATP in cells? üî¨',
    options: ['Photosynthesis', 'Cellular respiration', 'Osmosis', 'Diffusion'],
    correctAnswer: 'Cellular respiration'
  }
]

export default function Home() {
  const [currentQuestion, setCurrentQuestion] = useState(0)
  const [score, setScore] = useState(0)
  const [showScore, setShowScore] = useState(false)
  const [plantStage, setPlantStage] = useState('üå±')
  const [selectedDifficulty, setSelectedDifficulty] = useState('')
  const [gameStarted, setGameStarted] = useState(false)

  const handleDifficultySelect = (difficulty: string) => {
    setSelectedDifficulty(difficulty)
    setGameStarted(true)
  }

  const handleAnswerClick = (selectedAnswer: string) => {
    if (selectedAnswer === questions[currentQuestion].correctAnswer) {
      setScore(score + 1)
      // Update plant growth stages based on score
      if (score === 1) setPlantStage('üåø')
      if (score === 3) setPlantStage('üå≥')
    }

    const nextQuestion = currentQuestion + 1
    if (nextQuestion < questions.length) {
      setCurrentQuestion(nextQuestion)
    } else {
      setShowScore(true)
    }
  }

  const resetQuiz = () => {
    setCurrentQuestion(0)
    setScore(0)
    setShowScore(false)
    setPlantStage('üå±')
    setGameStarted(false)
    setSelectedDifficulty('')
  }

  const filteredQuestions = questions.filter(q => q.level === selectedDifficulty)

  if (!gameStarted) {
    return (
      <main className="min-h-screen p-8 bg-gradient-to-br from-green-50 via-blue-50 to-green-50">
        <div className="max-w-2xl mx-auto relative z-10">
          <Card className="p-8 text-center shadow-xl backdrop-blur-sm bg-white/90 border border-green-100">
            <h1 className="text-4xl font-bold mb-8 bg-gradient-to-r from-green-600 to-blue-600 bg-clip-text text-transparent">
              Biology Quiz <span role="img" aria-label="DNA" className="animate-bounce-subtle inline-block">üß¨</span>
            </h1>
            <p className="mb-6 text-gray-600">Choose your difficulty level:</p>
            <div className="space-y-4">
              <Button 
                onClick={() => handleDifficultySelect('easy')}
                className="w-full bg-gradient-to-r from-green-400 to-green-500 hover:from-green-500 hover:to-green-600 transform hover:scale-105 transition-all duration-200"
              >
                Easy <span role="img" aria-label="plant">üå±</span>
              </Button>
              <Button 
                onClick={() => handleDifficultySelect('medium')}
                className="w-full bg-gradient-to-r from-blue-400 to-blue-500 hover:from-blue-500 hover:to-blue-600 transform hover:scale-105 transition-all duration-200"
              >
                Medium <span role="img" aria-label="lab">üß´</span>
              </Button>
              <Button 
                onClick={() => handleDifficultySelect('hard')}
                className="w-full bg-gradient-to-r from-purple-400 to-purple-500 hover:from-purple-500 hover:to-purple-600 transform hover:scale-105 transition-all duration-200"
              >
                Hard <span role="img" aria-label="microscope">üî¨</span>
              </Button>
            </div>
          </Card>
        </div>
      </main>
    )
  }

  return (
    <main className="min-h-screen p-8 bg-gradient-to-br from-green-50 via-blue-50 to-green-50">
      <div className="max-w-2xl mx-auto relative z-10">
        {showScore ? (
          <Card className="p-8 text-center shadow-xl backdrop-blur-sm bg-white/90 border border-green-100">
            <h2 className="text-3xl font-bold mb-6 bg-gradient-to-r from-green-600 to-blue-600 bg-clip-text text-transparent flex items-center justify-center gap-2">
              Quiz Complete! <span role="img" aria-label="celebration" className="animate-bounce-subtle">üéâ</span>
            </h2>
            <div className="flex flex-wrap justify-center gap-3 mb-6">
              {Array.from({ length: score >= 4 ? 5 : score }).map((_, i) => (
                <span 
                  key={i} 
                  role="img" 
                  aria-label="leaf"
                  className="text-4xl inline-block animate-bounce-subtle"
                  style={{ 
                    animationDelay: `${i * 0.2}s`,
                    transform: `rotate(${i * 45}deg)`
                  }}
                >
                  üçÉ
                </span>
              ))}
            </div>
            <p className="text-2xl mb-6 text-gray-700">
              You scored <span className="font-bold text-green-600">{score}</span> out of <span className="font-bold text-green-600">{questions.length}</span>
            </p>
            {score >= 4 && (
              <div className="mb-6 p-6 bg-gradient-to-r from-green-100 to-blue-100 rounded-lg transform hover:scale-105 transition-transform duration-200">
                <p className="text-xl font-semibold mb-3 text-gray-800">
                  <span>Congratulations!</span>{' '}
                  <span role="img" aria-label="star" className="inline-block animate-bounce-subtle">‚≠ê</span>
                </p>
                <div className="text-2xl">
                  <p className="mb-2">You earned the Junior Biologist Badge</p>
                  <div className="flex justify-center gap-3 mt-2">
                    <span role="img" aria-label="microscope" className="inline-block animate-bounce-subtle">üî¨</span>
                    <span role="img" aria-label="test tube" className="inline-block animate-bounce-subtle" style={{ animationDelay: "0.2s" }}>üß™</span>
                    <span role="img" aria-label="dna" className="inline-block animate-bounce-subtle" style={{ animationDelay: "0.4s" }}>üß¨</span>
                  </div>
                </div>
              </div>
            )}
            <Button 
              onClick={resetQuiz}
              className="mt-6 bg-gradient-to-r from-green-400 to-green-500 hover:from-green-500 hover:to-green-600 transform hover:scale-105 transition-all duration-200 px-8 py-3 text-lg font-medium"
            >
              Try Again <span role="img" aria-label="restart" className="inline-block ml-2 animate-spin-slow">üîÑ</span>
            </Button>
          </Card>
        ) : (
          <Card className="p-8 shadow-xl backdrop-blur-sm bg-white/90 border border-green-100">
            <div className="text-right mb-6">
              <span className="text-4xl animate-bounce-subtle inline-block" role="img" aria-label="progress plant">{plantStage}</span>
            </div>
            <div className="mb-8">
              <div className="flex items-center justify-between mb-6">
                <h2 className="text-xl font-semibold text-gray-700">
                  Question {currentQuestion + 1}/{questions.length}
                </h2>
                <span className="px-3 py-1 bg-gradient-to-r from-green-100 to-blue-100 rounded-full text-sm font-medium text-gray-700">
                  {selectedDifficulty.charAt(0).toUpperCase() + selectedDifficulty.slice(1)} Level
                </span>
              </div>
              <p className="text-xl mb-6 text-gray-800 font-medium">{questions[currentQuestion].question}</p>
            </div>
            <div className="space-y-4">
              {questions[currentQuestion].options.map((option, index) => (
                <Button
                  key={index}
                  onClick={() => handleAnswerClick(option)}
                  className="w-full text-left bg-white hover:bg-gradient-to-r hover:from-green-50 hover:to-blue-50 text-gray-800 border border-gray-200 p-4 rounded-lg hover:shadow-md transition-all duration-200 hover:scale-[1.02] hover:border-green-200"
                >
                  <span className="inline-block w-6 h-6 text-sm bg-gradient-to-r from-green-100 to-blue-100 rounded-full mr-3 text-center leading-6">
                    {String.fromCharCode(65 + index)}
                  </span>
                  {option}
                </Button>
              ))}
            </div>
          </Card>
        )}
      </div>
    </main>
  )
}
